.inner-sorter {
  display: flex;
  flex-direction: column;
  margin-bottom: 27px;
  margin-top: 15px;

  @include viewport--xs {
    margin-top: 0;
  }

  &__column {
    display: flex;
    align-items: flex-start;

    font-family: $font-family--r;
    font-size: 18px;
    line-height: 26px;
    font-weight: 500;

    &:not(:last-child) {
      margin-bottom: 18px;

      @include viewport--xs {
        margin-bottom: 12px;
      }
    }

    @media (max-width: 1200px) {
      flex-wrap: wrap;
    }

    @include viewport--xb {
      position: relative;
      flex-wrap: wrap;
    }
  }

  &__find-offers {
    margin-top: 0;
    margin-bottom: 0;
    margin-right: 26px;
    max-width: 235px;
    letter-spacing: 0.027em;

    color: $color-eclipse;

    @media (max-width: 1199px) {
      width: calc(100% - 330px);
      max-width: none;
      margin-right: auto;
      margin-bottom: 20px;
    }

    @include viewport--xs {
      margin-bottom: 0;
      width: calc(100% - 150px);
      letter-spacing: -0.5px;

      span {
        display: none;
      }
    }
  }
}

.inner-sorter-map {
  flex-grow: 1;
}
