.catalog-title {
  margin-top: 0;
  margin-bottom: 15px;
  font-family: $font-family--m;
  font-size: 30px;
  line-height: 37px;
  font-weight: 700;
  letter-spacing: 0.026em;
  color: $color-night-rider;

  @include viewport--xs {
    font-size: 24px;
    line-height: 33px;
    margin-bottom: 7px;
  }
}

.catalog__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  padding-bottom: 30px;
}

.catalog__block {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin-bottom: 65px;

  @media (max-width: 766px) {
    justify-content: center;
  }

  @media (max-width: 360px) {
    justify-content: center;

    // .catalog__column {
    //   width: 304px;
    // }

    .catalog__container {
      height: 191px;
    }

    .catalog__card img {
      height: 195px;
    }

    .checkbox-other span::before {
      left: -29px;
    }

    .catalog__show {
      top: 26px;
      left: -13px;
    }

    .catalog__price {
      padding-bottom: 6px;
      font-size: 35px;
    }

    .catalog__address {
      font-size: 19px;
    }

    .button--catalog {
      margin-top: 19px;
    }

    .catalog__list {
      padding: 0 0 4px;
    }

    .catalog__block {
      margin-bottom: 35px;
      width: 292px;
    }
  }

  @media (max-width: 320px) {
    .catalog__show {
      left: -8px;
    }
  }
}

.catalog__description {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  width: 100%;
  padding: 28px 26px 17px 21px;
}

.catalog__column {
  display: flex;
  flex-direction: column;
  width: calc(22.5% - -16px);
  min-height: 345px;
  margin-bottom: 25px;
  border-radius: 15px;

  box-shadow: 0 0 4px $color-box-shadow;
  transition: all 0.3s ease;

  &:hover {
    box-shadow: 0 0 18px $color-box-shadow;
  }
}

.catalog__container {
  position: relative;
  height: 177px;
}

.catalog__card {
  position: relative;
  display: block;
}

.catalog__card img {
  width: 100%;
  height: 187px;
  border-radius: 15px 15px 0 0;
  object-fit: cover;
}

.catalog__show {
  position: absolute;
  display: block;
  width: 108px;
  min-height: 26px;
  font-weight: 500;
  box-shadow: -2px 1px 9px rgba(0, 3, 6, 0.39);
  border-radius: 4px;
  background-color: #3ba22f;
  font-family: "Roboto", "Arial", sans-serif;
  font-size: 12px;
  color: $color-white;
  line-height: 27px;
  border: 0;
  z-index: 80;
  text-align: center;
  top: 25px;
  left: -10px;
  padding: 0;
  will-change: transform;
}

.catalog__show:focus,
.catalog__show:hover {
  border: none;
  background-color: #3ba443;
  transition: all 0.3s;
  will-change: transform;
}

.catalog__price {
  margin: 0;
  -webkit-text-fill-color: $color-air-force-blue;
  color: $color-air-force-blue;
  font-size: 33px;
  font-weight: 700;
  text-align: center;
  font-style: normal;
  line-height: normal;
  padding-bottom: 7px;
}

.catalog__address {
  color: $color-night-rider-2;
  font-size: 18px;
  font-weight: 300;
  font-style: normal;
  letter-spacing: 0.025em;
  line-height: normal;
  min-height: 40px;
  text-align: center;
}

.catalog__item-list sup {
  left: 45px;
  font-size: 48%;
  top: -21px;
}

.catalog__item {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: space-between;
  color: $color-night-rider-2;
  font-size: 22px;
  font-weight: 900;
  line-height: 36px;
  margin-left: -8px;
  margin-right: -14px;
  letter-spacing: 0.03em;
}

.catalog__item-list {
  width: 33%;
  display: flex;
  flex-direction: column;
  text-align: center;
  position: relative;
  font-size: 20px;
}

.catalog__item-list:not(:first-of-type)::after {
  content: "";
  display: inline-block;
  width: 1px;
  height: 48px;
  border: 1px solid $color-pink-swan;
  position: absolute;
  margin-top: 13px;
  left: 6px;
}

.catalog__item-list span {
  padding-left: 10px;
  color: $color-black-2;
  font-family: $font-family--r;
  font-size: 17px;
  font-weight: 400;
  font-style: normal;
  line-height: 23px;
  text-align: center;
}

.catalog_like {
  width: 46px;
  height: 46px;
  position: absolute;
  z-index: 1;
  left: 89%;
  top: 10px;
}

.catalog-swiper__swiper-container {
  width: 100%;
  height: 100%;
  margin: 0;
  position: initial;
}

.catalog__swiper .swiper-wrapper {
  width: 292px;
  margin: 0;
  z-index: 0;
}

.catalog__swiper .swiper-pagination {
  width: 292px;
  display: flex;
  justify-content: space-between;
  z-index: initial;
  position: absolute;
  bottom: 0;
  top: 0;
}

.catalog__swiper .swiper-pagination-bullet {
  width: 24%;
  height: 185px;
  border-bottom: 3.5px solid $color-light-grey;
  border-radius: 0;
  opacity: 1;
  background: initial;
  transition: all 0.3s;
}

.catalog__swiper .swiper-pagination-bullet-active {
  border-bottom: 3px solid $color-allports;
  transition: all 0.3s;
}

.catalog__swiper .swiper-container-horizontal > .swiper-pagination-bullets .swiper-pagination-bullet {
  margin: 0;
}

.catalog__swiper .swiper-pagination-bullet:hover,
.catalog__swiper .swiper-pagination-bullet:focus,
.catalog__swiper .swiper-pagination-bullet-active:focus,
.catalog__swiper .swiper-pagination-bullet-active:hover {
  opacity: 0.7;
  transition: all 0.3s;
}

@media (max-width: 1289px) {
  .catalog__column {
    width: calc(33% - 15px);
  }

  .catalog__swiper .swiper-pagination {
    width: inherit;
  }

  .catalog__item-list {
    width: 33%;
  }

  .catalog__item-list:not(:first-of-type)::after {
    margin-left: 0;
  }
}

@media (max-width: $viewport--md) {
  .catalog__column {
    width: calc(49% - 3px);
  }
}

@media (max-width: $viewport--sm) {
  .catalog__column {
    width: 100%;
  }
}
