.filter-checkbox {
  margin-right: auto;
  transition: opacity $default-transition-settings;
  margin-right: 14px;
  margin-bottom: 14px;

  @media (max-width: $viewport--md) {
    margin-right: 13px;
    margin-bottom: 13px;
  }

  input[type="checkbox"] {
    position: absolute;

    width: 1px;
    height: 1px;
    margin: -1px;

    clip: rect(0 0 0 0);
  }

  label {
    display: block;
    padding: 10px;
    padding-left: 28px;
    position: relative;
    cursor: pointer;
    white-space: nowrap;
    line-height: 22px;

    &::before {
      content: "";
      position: absolute;
      top: 50%;
      left: 0;
      transform: translateY(-50%);
      width: 17px;
      height: 16px;
      border-radius: 5px;
      border: 1px solid $color-pink-swan;
      background-color: $color-default-white;
      background-repeat: no-repeat;
      background-position: top 50% left 50%;
    }
  }

  &--modal {
    margin-bottom: 4px;
  }

  input:checked + label {
    &::before {
      background-image: url("../img/svg/check.svg");
    }
  }

  &--border {
    border: 1px solid $color-light-grey;
    border-radius: 4px;

    label {
      padding-left: 40px;

      &::before {
        left: 14px;
      }
    }
  }

  &--small {
    font-size: 14px;
    line-height: 19px;
    letter-spacing: 0.4px;

    label {
      padding-right: 14px;
    }
  }

  &:hover {
    opacity: 0.7;
  }

  @include viewport--xs {
    margin-bottom: 0;
  }
}

.filter-input {
  flex-grow: 1;
  width: 319px;
  margin-right: 13px;
  margin-bottom: 13px;

  &--id {
    width: 145px;
    margin-right: 16px;

    @include viewport--xs {
      width: calc(50% - 13px);
      margin-right: 13px;
    }
  }

  &--sm {
    width: 80px;
    margin-right: 16px;

    @include viewport--xs {
      width: calc(50% - 13px);
      margin-right: 13px;
    }
  }

  @media (max-width: 1200px) {
    width: calc(50% - 13px);
    margin-right: 13px;
    margin-bottom: 13px;
  }

  @include viewport--xs {
    width: 96%;
  }

  input {
    width: 100%;
    flex-grow: 1;
    border: 1px solid $color-pink-swan;
    border-radius: 5px;
    padding: 10px 17px;
    font-family: $font-family--m;
    font-size: 15px;
    line-height: 21px;
    transition: border-color 0.3s;

    @media (max-width: 400px) {
      font-size: 13px;
    }

    &:hover {
      border: 1px solid $color-air-force-blue;
      // transition: all 0.3s;
    }

    &:focus {
      border: 1px solid $color-night-rider;
      outline: none;
      // transition: all 0.3s;
    }
  }
}

.filter-checkboxes {
  input[type="checkbox"] {
    position: absolute;

    width: 1px;
    height: 1px;
    margin: -1px;

    clip: rect(0 0 0 0);
  }

  label {
    display: block;
    padding-left: 26px;
    margin-bottom: 6px;
    position: relative;
    cursor: pointer;
    // white-space: nowrap;
    font-size: 15px;
    letter-spacing: 0.025em;

    &::before {
      content: "";
      position: absolute;
      top: 11px;
      left: 0;
      transform: translateY(-50%);
      width: 19px;
      height: 18px;
      border-radius: 5px;
      border: 1px solid $color-pink-swan;
      background-color: $color-default-white;
      background-repeat: no-repeat;
      background-position: top 50% left 50%;
      transition: border-color 0.3s;
    }

    &:hover {
      &::before {
        border-color: $color-air-force-blue;
      }
    }
  }

  input:checked + label {
    &::before {
      background-image: url("../img/svg/check.svg");
    }
  }

  @media (max-width: 1366px) {
    //right: 0;
    left: auto;
  }
}

.filter-checkboxes--tags {
  .filter-checkboxes__input {
    margin-right: 0;
    padding-right: 0;
    padding-bottom: 0;
    margin-bottom: 10px;
  }

  .filter-checkboxes__list {
    display: flex;
    flex-wrap: wrap;
    margin-top: 0;
    margin-bottom: 6px;
    padding-top: 0;
    max-height: 104px;
    // margin-right: -10px;
    overflow: overlay;

    &--active {
      max-height: 209px;
    }

    label {
      width: calc(50% - 10px);
      margin-right: 10px;
      margin-bottom: 2px;

      @media (max-width: $viewport--sm) {
        width: 100%;
        margin-right: 0;
      }
    }
  }
}

.filter-checkboxes--hide-tags {
  .filter-checkboxes__button-regular {
    display: none;
  }

  .filter-checkboxes__button-active {
    display: block;
  }

  .filter-checkboxes__list {
    max-height: 237px;
  }

  .filter-checkboxes__tags {
    display: none;
  }
}

.filter-checkboxes__button {
  display: block;
  background-color: transparent;
  border: none;
  padding: 0;
  text-decoration: underline;
  text-decoration-style: dotted;
  color: $color-air-force-blue;
  font-weight: 700;
  margin-bottom: 16px;
  font-size: 13px;
  line-height: 15px;
  cursor: pointer;
  transition: color 0.3s;
  letter-spacing: 0.3px;

  &:last-child {
    margin-bottom: 6px;
  }

  &:focus {
    outline: none;
  }

  &:hover {
    color: $color-default-black;
  }
}

.filter-checkboxes__button-active {
  display: none;
}

.filter-checkboxes__tags {
  display: flex;
  flex-wrap: wrap;
  margin-right: -9px;
  margin-bottom: 2px;

  label {
    background-color: lighten($color-air-force-blue, 35%);
    padding: 9px 17px 9px;
    border-radius: 4px;
    margin-right: 9px;
    margin-bottom: 7px;
    line-height: 21px;
    transition: box-shadow $default-transition-settings, background-color $default-transition-settings;

    &::before {
      content: none;
    }

    &:hover {
      box-shadow: 0 0 0 1px $color-venice-blue;
      //opacity: 0.8;
    }
  }

  input:checked + label {
    background-color: lighten($color-air-force-blue, 20%);

    &:hover {
      box-shadow: none;
      //opacity: 1;
    }
  }
}

.filter-checkboxes__input {
  width: auto;
  padding-right: 15px;
  padding-bottom: 11px;
  margin-bottom: 0;
  margin-right: 10px;
  background-color: $color-alice-blue;
  position: relative;
  z-index: 1;

  input {
    padding: 10px 9px;
    font-size: 14px;
    line-height: 18px;
  }
}

.filter-checkboxes__list {
  max-height: 237px;
  // overflow: auto;
  margin-top: -51px;
  padding-top: 51px;
  padding-right: 10px;

  &::-webkit-scrollbar {
    width: 10px;
  }

  &::-webkit-scrollbar-track {
    background: rgba($color-default-white, 0);
  }

  &::-webkit-scrollbar-thumb {
    background: $color-spindle;
  }

  &--no-mt {
    margin-top: 0;
    padding-top: 0;
  }

  &--small {
    max-height: 108px;
  }
}

.filter-range-row {
  display: flex;
  margin-bottom: 10px;

  &:last-child {
    margin-bottom: 0;
  }

  label {
    margin-right: 8px;
  }

  input {
    width: 100%;
    min-width: 0;
    background-color: $color-default-white;
    border-radius: 5px;
    border: 1px solid $color-pink-swan;
    padding: 2px 15px 3px;
    font-family: $font-family--m;
    font-size: 15px;
    line-height: 20px;

    &::placeholder {
      color: $color-pink-swan;
    }

    transition: border-color 0.3s;

    &:hover {
      border: 1px solid $color-air-force-blue;
    }

    &:focus {
      border: 1px solid $color-night-rider;
      outline: none;
    }
  }
}

.filter-multirange {
  label {
    display: block;
    margin-bottom: 7px;
    margin-left: 11px;
  }
}

.filter-multirange__inputs {
  display: flex;
  flex-wrap: wrap;
  position: relative;
  margin-bottom: 14px;
  border-radius: 5px;
  border: 1px solid $color-pink-swan;
  overflow: hidden;
  transition: border-color 0.3s;

  &:hover {
    border: 1px solid $color-air-force-blue;
  }

  &::before {
    content: "";
    position: absolute;
    top: 2px;
    bottom: 2px;
    left: 50%;
    transform: translateX(-1px);
    width: 2px;
    background-color: $color-alice-blue;
    z-index: 2;
  }

  input {
    width: 50%;
    flex-grow: 1;
    border: none;
    padding: 2px 5px 3px;
    font-family: $font-family--m;
    font-size: 14px;
    line-height: 20px;

    &:focus {
      outline: none;
    }
  }
}

.filter-text-input {
  min-width: 206px;

  input {
    width: 100%;
    flex-grow: 1;
    border: 1px solid $color-pink-swan;
    border-radius: 5px;
    padding: 2px 7px 3px;
    font-family: $font-family--m;
    font-size: 15px;
    line-height: 20px;
    transition: border-color 0.3s;

    &:hover {
      border: 1px solid $color-air-force-blue;
      // transition: all 0.3s;
    }

    &:focus {
      border: 1px solid $color-night-rider;
      outline: none;
      // transition: all 0.3s;
    }
  }
}

.filter-checkbox-row {
  display: flex;
  border-radius: 4px;
  padding: 4px 11px;
  border: 1px solid $color-light-grey;
}

.filter-checkbox-row__item {
  position: relative;
  flex-grow: 1;
  text-align: center;
  transition: opacity $default-transition-settings;

  &:hover {
    opacity: 0.7;
  }

  &::before {
    content: "";
    display: block;
    width: 1px;
    position: absolute;
    top: 4px;
    bottom: 4px;
    right: 0;
    background: rgba($color-denim, 0.1);
  }

  &:last-child {
    &::before {
      content: none;
    }
  }

  input {
    display: none;
  }

  label {
    display: block;
    border-radius: 4px;
    padding: 6px 9px;
    color: $color-night-rider;
    cursor: pointer;
    font-size: 14px;
    letter-spacing: 0.0286em;
    white-space: nowrap;
  }

  input:checked + label {
    color: $color-air-force-blue;
  }
}

.filter-range {
  border-radius: 4px;
  border: 1px solid $color-light-grey;
  display: flex;
  justify-content: space-between;
}

.filter-range__input {
  width: 50%;
  transition: opacity $default-transition-settings;

  &:hover {
    opacity: 0.7;
  }

  &:first-child {
    position: relative;

    &::after {
      content: "";
      position: absolute;
      right: -6px;
      top: 50%;
      transform: translateY(-50%);
      bottom: 6px;
      width: 16px;
      height: 1px;
      background-color: #cbcbcb;
      z-index: 1;
    }
  }

  label {
    display: none;
  }

  input {
    padding: 14px 12px;
    border-radius: 4px;
    font-size: 14px;
    background-color: $color-default-white;
    border: none;
    width: 100%;

    &:focus {
      outline: none;
      // transition: all 0.3s;
    }
  }
}

.filter-range__text {
  width: 38px;
  min-width: 38px;
  margin: 0;
  padding: 13px 5px;
  line-height: 19px;
  text-align: center;
  font-size: 14px;
}

.filter-large-input {
  display: flex;
  border-radius: 4px;
  border: 1px solid $color-light-grey;

  label {
    display: none;
  }

  input {
    flex-grow: 1;
    min-height: 44px;
    width: 331px;
    padding: 6px 14px;
    border: none;
    background-color: $color-default-white;
    font-size: 14px;
    letter-spacing: 0.4px;
    cursor: pointer;
    // transition: all 0.3s;

    @media (max-width: 1270px) {
      width: 250px;
    }

    @media (max-width: 340px) {
      font-size: 13px;
    }

    &:focus {
      outline: none;
    }

    &:hover {
      opacity: 0.7;
    }
  }
}

.filter-large-input__btns {
  display: flex;
  padding-right: 10px;
  margin-left: 10px;
}

.filter-big-address {
  @media (max-width: 490px) {
    display: none;
  }
}

.filter-small-address {
  display: none;

  @media (max-width: 490px) {
    display: block;
  }
}

.filter-large-input__btn {
  padding: 10px 6px;
  font-size: 14px;
  white-space: nowrap;
  transition: opacity $default-transition-settings;

  &--map {
    @media (max-width: 490px) {
      margin-left: -20px;
      font-size: 13px;
    }

    @media (max-width: 350px) {
      margin-left: -40px;
    }
  }

  &:hover {
    opacity: 0.7;
  }
}

.filter-input-text {
  display: flex;
  border-radius: 4px;
  border: 1px solid $color-light-grey;
  font-size: 14px;
  line-height: 19px;
  width: 100%;

  @media (max-width: 1022px) {
    margin-top: 13px;
  }

  @include viewport--xs {
    margin-top: 8px;
  }

  input {
    padding: 10px 14px;
    background-color: transparent;
    border: none;
    width: 125px;

    @media (max-width: 1022px) {
      width: 100%;
    }

  }
}

.filter-input-text__text {
  padding: 10px 14px;
  margin-left: auto;
}

.filter-checkbox.hide {
  @media (max-width: $viewport--xs) {
    label {
      white-space: normal;
    }
  }
}

.filter-dropdown-inner__label {
  display: flex;

  label {
    flex-grow: 1;
  }
}

.filter-dropdown-inner__btn {
  width: 23px;
  height: 24px;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0;
  border: none;
  background-color: transparent;
  cursor: pointer;

  svg {
    transform: rotate(180deg);
    fill: $color-blue-whale;
  }

  &:focus {
    outline: none;
  }
}

.filter-dropdown-inner__list {
  display: none;

  label {
    margin-left: 20px;
  }
}

.filter-dropdown-inner--open {
  .filter-dropdown-inner__btn {
    svg {
      transform: none;
    }
  }

  .filter-dropdown-inner__list {
    display: block;
  }
}

@media (max-width: 1366px) and (min-width: 1260px) {
  .filter-input--sd {
    margin-left: 3px;
    min-width: 237px;
  }
}

@media (max-width: 1259px) and (min-width: 1200px) {
  .filter-input--sd {
    margin-left: 3px;
    min-width: 159px;
  }
}
