.block-cards__container {
  @media (min-width: 1366px) {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  //    mobile
  @include viewport--md {
    margin-right: auto;
    margin-left: auto;

    width: 100%;
  }
}

.block-cards__list {
  margin: 0;
  padding: 0;
  list-style: none;

  @media (min-width: 1366px) {
    width: 100%;

    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: center;
  }

  //-    mobile
  @include viewport--md {
    margin-right: -100px;
    // margin-left: -20px;

    display: flex;
    justify-content: space-between;
    scroll-snap-type: x mandatory;

    // width: calc(100% + 120px);

    overflow: hidden;
    overflow-x: auto;

    //  
    &::-webkit-scrollbar,
    &::-webkit-scrollbar-track,
    &::-webkit-scrollbar-thumb,
    &::-webkit-scrollbar-thumb:hover {
      display: none;
      width: 0;
      height: 0;
      background-color: transparent;
    }
  }

  @media (max-width: 1365px) {
    margin-left: -118px;
    width: calc(100% + 228px);
  }

  @media (max-width: $viewport--md) {
    margin-left: -100px;
    width: calc(100% + 220px);
  }

  @media (max-width: $viewport--sm) {
    margin-left: -60px;
    width: calc(100% + 160px);
  }

  @media (max-width: $viewport--xs) {
    margin-left: -20px;
    width: calc(100% + 120px);
  }
}

.block-cards__item {
  //-    mobile
  @media (max-width: 1365px) {
    &:first-child {
      margin-left: 100px;
    }

    &:last-child {
      min-width: 410px;
    }
  }

  @media (max-width: $viewport--md) {
    &:first-child {
      margin-left: 80px;
    }
  }

  @media (max-width: $viewport--sm) {
    &:first-child {
      margin-left: 40px;
    }
  }

  @media (max-width: $viewport--xs) {
    &:first-child {
      margin-left: 0;
    }
  }
}

//  " "
.block-cards__btn-desktop {
  @include viewport--md {
    display: none;
  }
}
