.promo-filter {
  flex-wrap: wrap;
  color: $color-blue-whale;
  margin-right: -14px;
  letter-spacing: 1px;

  @include viewport--xs {
    margin-right: -13px;
  }

  @media (max-width: 374px) {
    flex-direction: column;
  }
}

.promo-filter__row {
  width: 100%;
  display: flex;
}

.promo-filter__more {
  position: relative;
  padding: 9px;
  padding-left: 32px;
  margin-right: 3px;
  margin-bottom: 13px;
  background-color: transparent;
  border: none;
  color: $color-air-force-blue;
  letter-spacing: 0.3px;
  cursor: pointer;

  @include viewport--xs {
    order: 1;
    margin-right: auto;
    margin-left: auto;
  }

  &:focus {
    outline: none;
  }

  &:hover,
  &:focus {
    opacity: 0.7;
    // transition: all 0.3s;
  }

  svg {
    position: absolute;
    top: 50%;
    left: 0;
    transform: translateY(-50%);
    fill: $color-air-force-blue;
  }

  .promo-filter__more-close {
    display: none;
  }
}

.promo-filter .hide {
  display: none;
}

.promo-filter--open {
  .promo-filter__more {
    padding-left: 25px;
  }

  .promo-filter__more-close {
    display: block;
  }

  .promo-filter__more-open {
    display: none;
  }

  .hide {
    display: block;
  }

  .promo-filter__checkbox--hide {
    display: block;
  }
}

.promo-filter__btn-map {
  width: 162px;
  margin-bottom: 13px;
  margin-right: 14px;
  margin-left: auto;
  color: $color-air-force-blue;
  border: 1px solid $color-air-force-blue;
  padding: 13px 14px;

  &:hover,
  &:focus {
    border: 1px solid $color-venice-blue;
    // transition: all 0.3s;
  }

  @include viewport--md {
    width: 212px;
  }

  @include viewport--sm {
    width: 120px;
  }

  @include viewport--xs {
    width: calc(50% - 13px);
    margin-right: 13px;
    margin-left: 0;
    font-size: 16px;
  }

  @media (max-width: $viewport--xs) {
    width: 96%;
  }

  &--wide-md {
    @include viewport--xs {
      width: 100%;
    }
  }
}

.promo-filter__btn-submit {
  width: 305px;
  margin-bottom: 13px;
  margin-right: 13px;
  background-color: $color-air-force-blue;
  padding: 13px 14px;

  @include viewport--sm {
    width: 295px;
  }

  @include viewport--xs {
    order: 1;
    width: 96%;
    font-size: 16px;
  }
}

.promo-filter__btns {
  display: flex;
  margin-left: auto;
}
