.search-mobile {
  display: none;
  flex-wrap: wrap;
  justify-content: space-between;
  border-bottom: 1px solid $color-gray-3;
  margin-bottom: 18px;

  @include viewport--xs {
    display: flex;
  }
}

.search-mobile__link {
  display: flex;
  align-items: center;
  padding: 8px 5px;
  margin-top: -12px;
  margin-right: auto;
  color: $color-air-force-blue;
  transition: color 0.3s;
  letter-spacing: 0.6px;

  &:hover {
    color: $color-gray-3;

    svg {
      fill: $color-gray-3;
    }
  }

  &:first-child {
    margin-left: -5px;
    margin-right: 8px;
  }

  &:last-child {
    margin-right: -5px;
  }

  svg {
    margin-right: 6px;
    transition: fill 0.3s;
    fill: $color-air-force-blue;
  }

  &--back {
    margin-right: auto;
    margin-left: 0;
    color: $color-gray-3;

    &:hover {
      color: $color-air-force-blue;

      svg {
        fill: $color-air-force-blue;
      }
    }

    svg {
      margin-bottom: 2px;
      transform: rotate(-90deg);
      fill: $color-gray-3;
    }
  }
}
