.small-flats__container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.small-flats__list {
  margin: 0;
  padding: 0;
  list-style: none;

  width: 100%;

  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;

  @media (max-width: 1365px) {
    justify-content: space-around;
  }

  @media (max-width: 1000px) {
    margin-top: -23px;
    flex-direction: column;
    justify-content: center;
  }
}

.small-flats__item {
  margin-bottom: 0;
  border-radius: 15px;

  @media (max-width: 1365px) {
    margin-right: 0;
    width: 40%;
  }

  @media (max-width: 1000px) {
    width: 100%;
  }

  @media (max-width: 400px) {
    margin-left: -15px;
  }

  @media (max-width: 360px) {
    margin-left: -23px;
  }

  @media (max-width: 320px) {
    margin-left: -25px;
  }
}

.small-flats .card-flat {
  @media (max-width: 1365px) {
    width: 100%;
  }
}

.small-flats .card-flat__shadow {
  @media (max-width: 1365px) {
    width: 90%;
  }

  @media (max-width: 1000px) {
    min-height: 458px;
    width: 92%;

    //- ""
    .check,
    .check__input,
    .check__box,
    .check__input:checked + .check__box {
      top: 10px;
      right: 10px;
    }
  }
}

.small-flats .card-flat .card-flat__link-online {
  top: 29px;
  left: 5px;
}

.small-flats .card-flat__box-swiper,
.small-flats .card-flat__box-swiper .swiper-container,
.small-flats .card-flat__box-swiper .swiper-wrapper {
  @media (max-width: 1000px) {
    height: 202px;

    img {
      object-fit: cover;
    }
  }
}

.small-flats .card-flat__btn {
  @media (max-width: 1365px) {
    width: 90%;
  }
}

.small-flats .card-flat__box-swiper .swiper-pagination-bullet {
  @media (max-width: 1365px) {
    width: 24%;
  }

  @media (max-width: 1000px) {
    height: 202px;
  }
}
