.socials {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  max-width: 286px;
  margin-top: 0;
  margin-bottom: 33px;
  padding-left: 0;
  list-style: none;

  a {
    opacity: 1;
    transition: opacity $default-transition-settings;

    &:hover {
      opacity: 0.7;
    }
  }

  @include viewport--xs {
    justify-content: flex-start;
  }
}


.socials__item {
  position: relative;
  margin-right: 15px;

  @include viewport--xs {
    margin-right: 17px;
  }
}

.socials__check-label {
  width: 15px;
  height: 15px;
  border-radius: 2px;
  border: 1px solid $color-air-force-blue;
  background-color: $color-default-white;
  position: absolute;
  z-index: 2;
  top: -6px;
  right: -6px;

  &::before {
    display: none;
    position: absolute;
    left: 11.6%;
    top: 9%;
    z-index: 3;
    // transform: translate(-50%, -50%);
    content: "";
    width: 13px;
    height: 13px;
    background-image: url("../img/svg/check.svg");
    background-repeat: no-repeat;
    background-size: 10px 10px;
  }
}

input:checked + .socials__check-label::before {
  display: block;
}

.socials__link {
  display: block;
  line-height: 0;
}
