.benefit {
  position: relative;
  padding-top: 46px;

  &__list {
    margin: 0;
    padding-left: 0;
    list-style: none;
  }

  &__item {
    display: flex;
    @media (max-width: $viewport--sm) {
      flex-direction: column;
    }
  }

  &__img {
    width: 409px;
    flex-shrink: 0;
    margin-right: 74px;
    padding-left: 96px;

    @media (max-width: $viewport--md) {
      width: 313px;
      padding-left: 0;
      margin-right: 40px;
    }

    @media (max-width: $viewport--sm) {
      margin-left: auto;
      margin-right: auto;
    }
  }

  &__description {
    width: 100%;
    max-width: 673px;
    padding-top: 37px;
    padding-bottom: 54px;

    h3 {
      margin: 0;
      margin-bottom: 17px;
      font-family: $font-family--m;
      font-size: 30px;
      line-height: 37px;
      font-weight: 700;
      letter-spacing: 0.025em;
      color: $color-default-black;
    }

    p {
      margin: 0;
      margin-bottom: 19px;
      font-family: $font-family--r;
      font-size: 16px;
      line-height: 22px;
      font-weight: 400;
      letter-spacing: 0.025em;
      color: $color-default-black;
    }

    p:first-child {
      font-family: $font-family--m;
      font-size: 21px;
      line-height: 30px;
      font-weight: 500;
      color: $color-night-rider;
    }

    p:last-child {
      margin-bottom: 0;
    }
  }
}

.benefit__pagination.swiper-pagination {
  position: absolute;
  display: inline-block;
  width: auto;
  height: 25px;
  left: 480px;
  bottom: 14px;

  @media (max-width: $viewport--md) {
    left: 353px;
  }

  @media (max-width: $viewport--sm) {
    left: 50%;
    transform: translateX(-50%);
  }

  .swiper-pagination-bullet {
    width: 13px;
    height: 13px;
    background-color: $color-onahau;
    opacity: 1;
    margin-right: 11px;
    vertical-align: middle;
    transition: all $default-transition-settings;

    &:hover {
      opacity: 0.7;
    }
  }

  .swiper-pagination-bullet:last-child {
    margin-right: 0;
  }

  .swiper-pagination-bullet-active {
    width: 23px;
    height: 23px;
    background-color: $color-default-white;
    border: 3px solid $color-jelly-bean;
    cursor: default;
    pointer-events: none;
  }
}
