.flat-online {
  padding-top: 44px;
  padding-bottom: 34px;
  overflow: hidden;

  @include viewport--xs {
    padding-top: 7px;
  }
}

.flat-online__title {
  margin-bottom: 45px;

  @include viewport--xs {
    margin-bottom: 25px;
  }
}

.flat-online__block {
  flex-direction: column;
  align-items: center;
  overflow: visible;
  display: none;
  width: 100%;

  &--show {
    display: flex;
  }

  //-    mobile
  @include viewport--md {
    padding-bottom: 7px;
  }
}

.flat-online__tabs {
  display: flex;
  justify-content: flex-start;
  margin-bottom: 35px;
  position: relative;

  @include viewport--md {
    margin-bottom: 0;
  }

  .flat-online__tabs-wrap {
    margin-left: 0;
    margin-bottom: 35px;

    @include viewport--md {
      margin-bottom: 0;
    }
  }
}

.flat-online__tabs-wrap {
  width: auto;
  position: relative;

  @include viewport--xs {
    margin-bottom: 21px;
  }

  &::before {
    content: "";
    position: absolute;
    width: 100%;
    height: 2px;
    background-color: $color-pattens;
    bottom: 0;
    left: 0;
  }
}

.flat-online__tab {
  padding: 0 6px 13px;
  margin-right: 27px;
  background-color: transparent;
  border: none;
  font-family: $font-family--m;
  color: $color-night-rider;
  font-weight: 500;
  font-size: 18px;
  line-height: 22px;
  letter-spacing: 0.016em;
  white-space: nowrap;
  cursor: pointer;
  position: relative;

  @include viewport--xs {
    margin-right: 7px;
    font-size: 16px;
  }

  &::before {
    content: "";
    width: 100%;
    position: absolute;
    bottom: 0;
    left: 0;
    height: 2px;
    background-color: $color-air-force-blue;
    opacity: 0;
    transition: opacity 0.3s;
  }

  &:not(.flat-online__tab--active) {
    &:hover {
      &::before {
        opacity: 0.5;
        transition: all 0.3s;
      }
    }
  }

  &--active {
    font-weight: bold;

    &::before {
      opacity: 1;
    }
  }

  &:focus {
    outline: none;
    transition: all 0.3s;
  }

  &:last-child {
    margin-right: 0;
  }
}

.flat-online__nav-btn {
  position: absolute;
  top: 0;
  left: -3px;

  display: flex;
  align-items: center;
  justify-content: flex-start;
  width: 30px;
  height: 30px;
  background: linear-gradient(90deg, $color-default-white, $color-default-white 50%, rgba(255, 255, 255, 0));
  z-index: 1;
  outline: none;
  cursor: pointer;

  &[aria-disabled="true"] {
    display: none;
  }

  svg {
    margin-bottom: 6px;
    transform: rotate(-90deg);
    fill: $color-blue-whale;
  }

  &--next {
    left: auto;
    right: -3px;
    justify-content: flex-end;
    background: linear-gradient(-90deg, $color-default-white, $color-default-white 50%, rgba(255, 255, 255, 0));
    outline: none;

    svg {
      transform: rotate(90deg);
    }
  }
}

.flat-online__content {
  .block-cards__list {
    @media (min-width: 1366px) {
      margin-top: -46px;
      margin-bottom: 26px;
    }
  }

  .block-cards__item {
    @media (min-width: 1366px) {
      margin-bottom: 10px;
    }
  }
}
