.sorting {
  flex-shrink: 0;
  display: flex;
  justify-content: flex-end;
  flex-wrap: wrap;
  align-items: flex-start;

  margin: 0;
  padding-left: 0;
  list-style: none;

  @media (max-width: 1199px) {
    margin-bottom: 20px;
    margin-right: 0;
  }

  @include viewport--xs {
    margin-bottom: 0;
    width: 150px;
  }
}

.sorting__item {
  position: relative;
  display: flex;
  margin-right: 26px;
  margin-bottom: 10px;
  max-width: 158px;

  color: $color-air-force-blue;

  @include viewport--xs {
    margin-right: 0;
    margin-bottom: 0;
  }

  &:last-child {
    margin-right: 0;
  }

  &:hover,
  &:focus,
  &:active {
    color: $color-gray-3;
    transition: $default-transition-settings;
  }

  svg {
    display: block;
    margin-right: 5px;
    flex-shrink: 0;
  }

  &--map {
    @include viewport--xs {
      display: none;
    }
  }
}

.sorting__button {
  padding: 0;
  font-size: 18px;
  line-height: 25px;
  letter-spacing: 0.038em;

  color: $color-air-force-blue;
  background: none;
  align-items: flex-start; //     

  @include viewport--xs {
    font-size: 16px;
    line-height: 24px;
    border-bottom: none;
  }

  &:hover,
  &:focus,
  &:active {
    background: none;
    color: $color-gray-3;
    transition: $default-transition-settings;
  }

  span {
    display: block;
    border-bottom: 1px dotted $color-air-force-blue;

    @include viewport--xs {
      border-bottom: none;
    }
  }
}
