.sorting-by-tag {
  align-items: flex-start;
  width: 100%;

  &__list {
    display: flex;
    align-items: flex-start;
    flex-wrap: wrap;

    padding: 0;
    margin: 0;
    margin-left: -10px;
    list-style-type: none;

    input[type="checkbox"] {
      position: absolute;

      width: 1px;
      height: 1px;
      margin: -1px;

      clip: rect(0 0 0 0);
    }

    input:checked + label {
      color: $color-default-white;
      background-color: lighten($color-air-force-blue, 20%);
      border: none;
    }
  }

  &__item {
    display: flex;
    max-width: 200px;

    margin-left: 10px;
    margin-bottom: 10px;

    font-family: $font-family--r;
    font-size: 15px;
    line-height: 19px;
    letter-spacing: 0.023em;

    color: $color-dim-gray;

    &:active {
      background-color: $color-nepal;
      color: $color-default-white;
      outline: none;
      //  ios
      border-radius: 4px;
    }
  }

  &__link {
    padding: 12px 20px 14px;
    color: $color-dim-gray;
    background-color: $color-alice-blue;
    border-radius: 4px;
    transition: all $default-transition-settings;
    font-size: 15px;
    height: 45px;
    text-align: center;

    &:hover,
    &:focus {
      //color: $color-default-white;
      //background-color: $color-venice-blue;
      box-shadow: 0 0 0 1px #35506f;
    }

    &:active {
      //background-color: $color-venice-blue;
      //color: $color-default-white;
      background-color: #9eb2c4;
    }
  }
}
