.filter-dropdown {
  flex-grow: 1;
  position: relative;
  width: 180px;
  margin-right: 12px;
  margin-bottom: 14px;

  &--district {
    width: 143px;
  }

  &--deadline {
    width: 110px;
  }

  &--street {
    width: 139px;

    @include viewport--md {
      width: 233px;
    }
  }

  &--complex {
    width: 233px;
  }

  &--floor {
    width: 131px;
  }

  &--bathroom {
    width: 152px;
  }

  @media (max-width: 1200px) {
    width: calc(50% - 13px);
    margin-right: 13px;
    margin-bottom: 13px;

    &--complex {
      width: 100%;
    }
  }

  &--modal {
    width: 329px;

    .filter-dropdown__btn {
      //padding-top: 10px;
      //padding-bottom: 10px;
      padding-top: 8px;
      padding-bottom: 8px;

      @media (max-width: $viewport--xs) {
        padding-left: 8px;
      }

      span {
        display: inline-block;
        border-radius: 3px;
        min-height: 22px;
        line-height: 22px;
        padding-left: 6px;
        padding-right: 6px;
        vertical-align: middle;

        @media (max-width: $viewport--xs) {
          background-color: $color-onahau;
        }
      }
    }


    @media (max-width: 1200px) {
      width: 329px;
    }
  }

  @media (max-width: 374px) {
    width: 96%;
  }
}

.filter-dropdown--sm,
.filter-dropdown--md,
.filter-dropdown--lg {
  &.filter-dropdown--open {
    .filter-dropdown__btn {
      border-bottom: none;
      z-index: 4;
      position: relative;
    }
  }

  .filter-dropdown__content {
    top: calc(100% - 2px);
    min-width: 400px;
    width: calc(100% + 20px);
    border-radius: 0 6px 6px 6px;

    @media (max-width: $viewport--md) {
      width: calc(100vw - 185px);
      min-width: auto;
      border-radius: 0 0 6px 6px;
    }

    @media (max-width: 767px) {
      width: calc(100vw - 86px);
    }

    @media (max-width: $viewport--xs) {
      width: calc(100vw - 46px);
    }
  }
}

.filter-dropdown--sm {
  .filter-dropdown__content {
    min-width: 200px;

    @media (max-width: $viewport--md) {
      width: calc(100% + 20px);
      border-radius: 0 6px 6px 6px;
    }

    @media (max-width: $viewport--xs) {
      width: calc(100% + 20px);
    }
  }
}

.filter-dropdown--md {
  .filter-dropdown__content {
    min-width: 245px;
  }
}

.filter-dropdown__btn {
  position: relative;
  width: 100%;
  min-width: 150px;
  padding: 12px 30px 12px 19px;
  border: 1px solid $color-pink-swan;
  background-color: $color-default-white;
  border-radius: 6px;
  color: $color-blue-whale;
  text-align: left;
  letter-spacing: 0.9px;
  cursor: pointer;
  z-index: 2;
  white-space: nowrap;
  transition: border $default-transition-settings;

  @include viewport--xs {
    padding: 12px 30px 13px 15px;
  }

  &:hover {
    // opacity: 0.7;
    // color: $color-night-rider;

    border: 1px solid $color-air-force-blue;
  }

  svg {
    position: absolute;
    top: 50%;
    right: 18px;
    transform: translateY(-50%) rotate(180deg);
    fill: $color-gray-3;

    @include viewport--xs {
      right: 8px;
    }
  }

  &:focus {
    outline: none;
  }

  @media (max-width: 400px) {
    font-size: 16px;
  }
}

.filter-dropdown__hide-sm {
  @media (max-width: $viewport--xs) {
    display: none;
  }
}

.filter-dropdown__show-sm {
  display: none;

  @media (max-width: $viewport--xs) {
    display: block;
  }
}

.filter-dropdown__content {
  display: none;
  position: absolute;
  top: calc(100% - 1px);
  min-width: 100%;
  padding: 6px 10px;
  border: 1px solid $color-pink-swan;
  background-color: $color-default-white;
  border-radius: 0 0 6px 6px;
  z-index: 3;

  @include viewport--xs {
    padding: 10px 8px;
    min-width: 100%;
    min-height: 47px;
  }
}

.filter-dropdown--right {
  .filter-dropdown__content {
    // right: 0;
    left: auto;
  }

  &.filter-dropdown--md,
  &.filter-dropdown--lg {
    .filter-dropdown__content {
      border-radius: 6px 0 6px 6px;
    }
  }

  @include viewport--sm {
    .filter-dropdown__content {
      left: 0;
      right: auto;
    }

    &.filter-dropdown--md,
    &.filter-dropdown--lg {
      .filter-dropdown__content {
        border-radius: 0 6px 6px 6px;
      }
    }
  }
}

.filter-dropdown---right-md {
  @include viewport--sm {
    .filter-dropdown__content {
      right: 0;
      left: auto;
    }

    &.filter-dropdown--md,
    &.filter-dropdown--lg {
      .filter-dropdown__content {
        border-radius: 6px 0 6px 6px;
      }
    }
  }
}

.filter-dropdown__overlay {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  z-index: 1;
}

.filter-dropdown--open {
  .filter-dropdown__btn {
    background-color: $color-alice-blue;
    border-radius: 6px 6px 0 0;

    svg {
      transform: translateY(-50%);
    }

    &:hover {
      border-color: $color-pink-swan;
    }
  }

  .filter-dropdown__content {
    display: block;
  }

  .filter-dropdown__overlay {
    display: block;
  }

  .filter-dropdown__group {
    display: flex;
    padding: 3px 5px 3px 0;

    &--btn {
      padding: 16px 5px 10px 10px;
    }

    label {
      display: inline-block;
      display: block;
      width: auto;
      padding: 5px 12px;
      border-radius: 4px;
      white-space: nowrap;
      font-size: 14px;
      line-height: 17px;
    }

    label::before {
      display: none;
    }

    input:checked + label {
      background-color: $color-alice-blue;
    }
  }
}

.filter-dropdown--small {
  font-size: 14px;
  line-height: 19px;

  .filter-dropdown__btn {
    padding: 14px 30px 14px 14px;
    border: 1px solid $color-light-grey;

    svg {
      right: 11px;
    }
  }

  .filter-dropdown__time-btn {
    padding-top: 11px;
    padding-bottom: 11px;
  }

  .filter-dropdown__content {
    border: 1px solid $color-light-grey;
  }
}

.filter-dropdown-single-choices {
  .choices::after {
    margin-top: -2.5px;

    position: absolute;
    right: 11.5px;
    top: 50%;

    content: "";
    width: 13px;
    height: 7px;
    background-image: url("../img/svg/filter-arr-down.svg");
    background-size: 13px 7px;
    background-repeat: no-repeat;

    pointer-events: none;
  }

  .choices.is-open::after {
    margin-top: -2.5px;

    position: absolute;
    right: 11.5px;
    top: 50%;

    content: "";
    width: 13px;
    height: 7px;
    background-image: url("../img/svg/filter-arr-down.svg");
    transform: rotate(180deg);
    background-size: 13px 7px;
    background-repeat: no-repeat;

    pointer-events: none;
  }

  .choices__inner {
    margin-top: 8px;

    color: #112f49;
    background-color: $color-default-white;
    border-radius: 6px;
    transition: background-color $default-transition-settings, border $default-transition-settings;

    @media (min-width: 1200px) {
      width: 400px;
    }

    &:hover,
    &:focus {
      border: 1px solid #5f7f9d;
      background-color: #f2f6f9;
    }
  }

  .choices.is-open .choices__inner {
    border-radius: 6px 6px 0 0;
  }

  .choices__list--dropdown .choices__item--selectable.is-highlighted {
    background-color: $color-default-white;
  }

  .choices__item {
    display: inline-block;
    vertical-align: middle;

    border-radius: 3px;
    min-height: 22px;
    line-height: 22px;
    padding-left: 6px;
    padding-right: 6px;
    margin-right: 6px;
    background-color: #c8dae1;
  }

  .choices__list {
    overflow-x: hidden;
  }

  .choices__list--dropdown .choices__item {
    background-color: $color-default-white;
    width: 100%;
  }

  .choices__list--single .choices__item {
    width: auto;
  }

  .choices__item--choice {
    transition: color $default-transition-settings;
  }

  .choices__item--choice:hover,
  .choices__item--choice:focus {
    color: #5f7f9d;
  }
}

.filter-dropdown__span-time {
  display: inline-block;
  vertical-align: middle;

  min-height: 22px;
  line-height: 20px;

  padding-left: 6px;
  padding-right: 6px;
}

.filter-dropdown__span-time-active {
  border-radius: 3px;
  background-color: #c8dae1;
}
