.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 200;

  display: flex;
  flex-direction: column;
  overflow-y: auto;

  background-color: rgba($color-dim-gray, 0.29);
  transform: scale(1.2);
  visibility: hidden;
  opacity: 0;
  pointer-events: none;

  transition: opacity $modal-transition-settings,
    transform $modal-transition-settings,
    visibility $modal-transition-settings;

  -ms-overflow-style: none;
  scrollbar-width: none;

  &::-webkit-scrollbar {
    display: none;
  }

  &__wrapper {
    position: relative;

    display: flex;
    flex-shrink: 0;
    justify-content: center;
    align-items: center;
    width: 100%;
    min-height: 100%;
    padding: 60px 40px;

    @include viewport--sm {
      padding: 60px 16px;
    }
  }

  &__overlay {
    position: absolute;
    top: 0;
    left: 0;

    width: 100%;
    height: 100%;

    cursor: pointer;
  }

  &__content {
    position: relative;

    width: 600px;
    padding: 40px;

    background-color: $color-default-white;

    @include viewport--sm {
      width: 100%;
      padding: 20px;
    }
  }

  &__close-btn {
    position: absolute;
    top: 15px;
    right: 31px;
    width: 22px;
    height: 22px;

    &::before,
    &::after {
      content: "";
      position: absolute;
      top: 50%;
      left: 0;
      width: 100%;
      height: 2px;
      background-color: $color-night-rider-2;
    }

    &::before {
      transform: rotate(45deg);
    }

    &::after {
      transform: rotate(-45deg);
    }

    @include viewport--xs {
      right: 13px;
    }
  }

  &--responsive {
    .modal__content {
      width: 100%;
    }
  }

  &--fit-content {
    .modal__content {
      width: auto;

      @include viewport--sm {
        width: 100%;
      }
    }
  }

  &--no-scale {
    transform: none;
  }

  &--preload {
    display: none;
  }

  &--active {
    transform: scale(1);
    visibility: visible;
    opacity: 1;
    pointer-events: auto;
  }
}

.modal--filter.modal--active {
  overflow: hidden;
}

.modal--filter {
  .modal__wrapper {
    //max-height: 100%;

    @include viewport--xs {
      padding: 0;
    }
  }

  .modal__content {
    padding: 66px 19px 29px;
    padding-left: 0;
    padding-right: 0;

    width: 1067px;
    min-width: 1067px;
    height: 100vh;
    height: calc(100 * var(--vh, 1vh) - 200px);

    font-size: 14px;
    line-height: 19px;
    letter-spacing: 0.025em;

    border-radius: 6px;
    overflow-x: hidden;
    overflow-y: auto;

    @media (max-width: 1200px) {
      width: 1067px;
      min-width: 1067px;
      height: 100vh;
      height: calc(100 * var(--vh, 1vh));
    }

    @media (max-width: 1100px) {
      width: 700px;
      min-width: 700px;
    }

    @include viewport--sm {
      padding-top: 9px;
      width: 600px;
      min-width: 600px;
    }

    @include viewport--xs {
      padding: 8px 13px 10px;
      width: 100%;

      min-width: 100%;
      border-radius: 0;
    }
  }
}

.modal__btn-fix {
  margin: 0 auto;
  padding: 10px 20px 10px 15px;

  width: 1067px;
  min-width: 1067px;

  position: fixed;
  bottom: calc(100vh - 88vh);
  left: 0;
  right: 0;

  display: flex;
  flex-direction: row;

  background-color: $color-default-white;
  border-bottom-left-radius: 6px;
  border-bottom-right-radius: 6px;
  z-index: 200;

  @media (max-width: 1200px) {
    width: 1067px;
    min-width: 1067px;
    bottom: 0;
  }

  @media (max-width: 1100px) {
    width: 700px;
    min-width: 700px;
  }

  @include viewport--sm {
    width: 600px;
    min-width: 600px;
    height: 61px;
  }

  @include viewport--xs {
    width: 100%;
    min-width: 100%;
  }
}
