.search-filter {
  @include viewport--xs {
    display: none;
    margin-bottom: 16px;

    &--open {
      display: block;
    }
  }
}

.search-filter__row {
  display: flex;

  &--btns {
    justify-content: flex-end;
  }

  @media (max-width: 1100px) {
    flex-wrap: wrap;
    margin-right: -14px;
  }

  @include viewport--xs {
    margin-right: 0;
  }
}

.search-filter__item {
  margin-right: 15px;
  margin-bottom: 14px;

  &:last-child {
    margin-right: 0;
  }

  .filter-dropdown__btn {
    color: $color-night-rider-2;
  }

  @media (max-width: 1100px) {
    margin-right: 14px;

    &--lg {
      width: 100%;
    }

    &--md {
      width: calc(50% - 14px);
    }

    &--sm {
      width: calc(25% - 7px);
    }

    &:last-child {
      margin-right: 14px;
    }
  }

  @include viewport--xs {
    margin-right: 0;

    &--md,
    &--sm {
      width: 100%;
    }

    &:last-child {
      margin-right: 0;
    }
  }
}

.search-filter__btn {
  font-size: 14px;
  line-height: 19px;
  padding: 12px 26px;

  @include viewport--xs {
    width: 100%;
  }
}

