.sorting-by-quantity {
  flex-shrink: 0;
  display: flex;
  align-items: flex-start;
  margin-right: 28px;
  margin-left: auto;

  @include viewport--xs {
    display: none;
  }

  span {
    display: block;
    width: 138px;
    margin-top: 5px;
    margin-right: 7px;
    font-family: $font-family--m;
    font-size: 18px;
    line-height: 18px;
    font-weight: bold;
    letter-spacing: 0.027em;
  }

  &__list {
    display: flex;
    flex-wrap: wrap;
    width: 212px; //         

    padding: 0;
    margin: 0;
    margin-top: -17px;
    margin-left: -3px;
    list-style-type: none;
  }

  &__link {
    display: block;
    padding: 13px 15px;
    font-size: 14px;
    line-height: 14px;

    color: $color-gray-2;
    border-bottom: 2px solid $color-gray-2;

    &:hover,
    &:focus {
      color: $color-air-force-blue;
      border-bottom: 2px solid $color-air-force-blue;
      transition: $default-transition-settings;
    }
  }

  &__item {
    margin-left: 3px;
    min-height: 38px;
    min-width: 44px;

    &--active {
      .sorting-by-quantity__link {
        color: $color-air-force-blue;
        border-bottom: 2px solid $color-air-force-blue;
      }
    }
  }
}
