.visually-hidden {
  position: absolute;

  width: 1px;
  height: 1px;
  margin: -1px;

  clip: rect(0 0 0 0);
}

.wrapper {
  position: relative;

  display: flex;
  flex-direction: column;
  min-height: 100vh;
  min-height: calc(100 * var(--vh, 1vh));
  overflow-x: hidden;

  &--no-scrollbar {
    height: calc(100 * var(--vh, 1vh));

    -ms-overflow-style: none;
    scrollbar-width: none;

    &::-webkit-scrollbar {
      display: none;
    }
  }

  .header,
  .footer {
    flex-shrink: 0;
  }

  main {
    flex-grow: 1;
  }
}

.scroll-lock {
  position: fixed;

  overflow-y: hidden;
}

.btn-reset {
  padding: 0;

  background: $color-transparent;
  border: none;
  cursor: pointer;
}

@keyframes disableBreakpointAnimation {
  0% {
    visibility: hidden;
    opacity: 0;
  }

  100% {
    visibility: hidden;
    opacity: 0;
  }
}

.main-title {
  margin-top: 0;
  margin-bottom: 44px;
  font-size: 30px;
  line-height: 36px;
  font-family: $font-family--m;
  letter-spacing: 0.8px;

  @include viewport--sm {
    font-size: 24px;
    line-height: 29px;
    margin-bottom: 24px;
  }
}

.form-input {
  span {
    display: none;
  }

  input {
    display: block;
    width: 100%;
    padding: 12px 28px;
    border: 1px solid $color-light-grey;
    border-radius: 6px;
    color: $color-night-rider;
    font-size: 16px;
    line-height: 1.15;
    transition: border-color 0.3s;

    &:hover {
      border: 1px solid $color-air-force-blue;
    }

    &:focus {
      border: 1px solid $color-night-rider;
      outline: none;
      transition: all 0.3s;
    }

    &::placeholder {
      color: $color-night-rider;
    }

    @include viewport--xs {
      &::placeholder {
        color: $color-light-grey;
      }
    }
  }
}

.clearfix::after {
  content: ".";
  display: block;
  height: 0;
  clear: both;
  visibility: hidden;
}

.logo-active {
  &:hover,
  &:focus {
    opacity: 0.9;
    transition: $default-transition-settings;
  }

  &:active {
    opacity: 0.8;
  }
}

.rubl {
  font-family: $font-family--rubl;
}

::-webkit-scrollbar-button {
  width: 9px;
  height: 0;
}

/*  ,     . */
::-webkit-scrollbar-track {
  background-color: #ecedee;
  border-radius: 6px;
}

/*   ,     . */
::-webkit-scrollbar-thumb {
  -webkit-border-radius: 0;
  border-radius: 6px;
  background-color: $color-spindle;
}

/*       . */
::-webkit-scrollbar-thumb:hover {
  background-color: $color-air-force-blue;
}

/*    . */
::-webkit-resizer {
  width: 8px;
  height: 0;
}

::-webkit-scrollbar {
  width: 8px;
}

.tab-content {
  display: none;
}

.active-content {
  display: block;
}
