.news {
  padding-top: 66px;
  padding-bottom: 98px;
  overflow: hidden;

  @include viewport--xs {
    padding-top: 29px;
    padding-bottom: 45px;
  }
}

.news__title {
  span {
    display: none;
  }

  @include viewport--md {
    margin-bottom: 15px;
  }

  @include viewport--xs {
    font-size: 0;
    line-height: 0;

    span {
      display: block;
      font-size: 24px;
      line-height: 29px;
    }
  }
}

.news__list {
  @media (min-width: 1366px) {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    row-gap: 22px;
    column-gap: 15px;
    padding-left: 0;
    margin: 0;
    list-style: none;
  }

  //-    mobile
  @include viewport--md {
    padding: 0;
    margin-right: -100px;

    display: flex;
    justify-content: space-between;

    overflow: hidden;
    overflow-x: auto;

    //  
    &::-webkit-scrollbar,
    &::-webkit-scrollbar-track,
    &::-webkit-scrollbar-thumb,
    &::-webkit-scrollbar-thumb:hover {
      display: none;
      width: 0;
      height: 0;
      background-color: transparent;
    }
  }

  @media (max-width: 1365px) {
    margin-left: -118px;
    width: calc(100% + 228px);
  }

  @media (max-width: $viewport--md) {
    margin-left: -100px;
    width: calc(100% + 220px);
  }

  @media (max-width: $viewport--sm) {
    margin-left: -60px;
    width: calc(100% + 160px);
  }

  @media (max-width: $viewport--xs) {
    margin-left: -20px;
    width: calc(100% + 120px);
  }
}

.news__item {
  list-style: none;
  border-radius: 6px;
  box-shadow: 0 0 9px 0 rgba($color-default-black, 0.16);

  .news__header {
    font-weight: 700;
  }

  &--large {
    @media (min-width: 1366px) {
      grid-column-start: 1;
      grid-column-end: span 2;
      grid-row-start: 1;
      grid-row-end: span 2;

      .news__content {
        padding: 35px 27px 27px 32px;
      }

      .news__date {
        margin-bottom: 7px;
        font-size: 22px;
        line-height: 29px;
      }

      .news__header {
        max-height: (29px * 3);
        max-width: 441px;
        font-size: 24px;
        line-height: 29px;
        -webkit-line-clamp: 3;
      }
    }
  }
}

.block-cards .news__item {
  //-    mobile
  @include viewport--md {
    width: 320px;
    height: 340px;

    border-radius: 15px;
    box-shadow: none;
  }
}

.block-cards .news__item__shadow {
  //-    mobile
  @include viewport--md {
    margin: 20px;
    width: 288px;
    height: 300px;

    border-radius: 15px;
    box-shadow: 0 0 4px $color-box-shadow;
    transition: box-shadow $default-transition-settings;

    &:hover {
      box-shadow: 0 0 18px $color-box-shadow;
    }
  }
}

.news__link {
  &:hover {
    .news__img {
      img {
        transform: scale(1.05);
      }
    }
  }
}

.block-cards .news__link {
  &:hover {
    .news__img {
      img {
        transform: none;
      }
    }
  }
}

.news__img {
  height: 0;
  padding-bottom: 72.575%;
  position: relative;

  @include viewport--xs {
    padding-bottom: 71%;
  }

  img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    transition: transform 0.3s;
    object-fit: cover;
  }
}

.block-cards .news__img {
  //-    mobile
  @include viewport--md {
    width: 288px;
    height: 208px;
    border-top-right-radius: 15px;
    border-top-left-radius: 15px;
    padding: 0;

    img {
      width: 288px;
      height: 208px;
      border-top-right-radius: 15px;
      border-top-left-radius: 15px;
    }
  }
}

.news__content {
  padding: 10px 25px 26px 13px;

  //-    mobile
  @include viewport--md {
    overflow: hidden;
  }

  @include viewport--xs {
    padding: 9px 25px 26px 9px;
  }
}

.news__date {
  display: block;
  margin-bottom: 2px;
  font-size: 13px;
  line-height: 18px;
  font-weight: 500;
  color: $color-denim;
  letter-spacing: 0.2px;
}

.news__header {
  display: -webkit-box;
  margin: 0;
  max-height: (17px * 2);
  font-size: 15px;
  line-height: 17px;
  letter-spacing: 0.3px;
  text-overflow: ellipsis;
  overflow: hidden;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
}
