.card-flat {
  min-width: 300px;
  min-height: 434px;
  border-radius: 15px;
  background-color: $color-default-white;
  position: relative;

  //- "-"
  .card-flat__link-online {
    position: absolute;
    top: 25px;
    left: 3px;

    display: inline-block;
    vertical-align: middle;

    width: 108px;
    height: 25px;

    text-align: center;
    border-radius: 4px;
    background-color: $color-la-palma;
    box-shadow: -2px 1px 9px rgba(0, 3, 6, 0.388);
    z-index: 3;

    font-size: 12px;
    line-height: 24px;
    font-weight: bold;
    color: $color-default-white;

    transition: box-shadow $default-transition-settings;

    //-    mobile
    @include viewport--md {
      // margin    hover
      left: 12px;
    }

    &:hover,
    &:focus {
      box-shadow: none;
    }
  }

  .card-flat__wrapper {
    border-radius: 15px;
  }
}

.card-flat__shadow {
  // margin    hover
  margin: 7px;

  width: 288px;
  min-height: 434px;

  position: relative;

  border-radius: 15px;
  box-shadow: 0 0 4px $color-box-shadow;
  transition: box-shadow $default-transition-settings;

  &:hover {
    box-shadow: 0 0 18px $color-box-shadow;
  }

  //-    mobile
  @include viewport--md {
    // margin    hover
    margin: 20px;
  }

  //- ""
  .check {
    display: block;

    position: absolute;
    top: 10px;
    right: 5px;
    z-index: 3;

    cursor: pointer;
    user-select: none;
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  }

  .check__input {
    position: absolute;
    top: 10px;
    right: 5px;

    width: 46px;
    height: 46px;
    border: none;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    outline: none;
    background-color: inherit;
  }

  .check__box {
    position: absolute;
    top: 10px;
    right: 5px;

    width: 46px;
    height: 46px;

    outline: none;
    background-image: url("../img/sprite/icon-like.svg");
    border-radius: 50%;
    background-color: inherit;
  }

  /* Checked */

  .check__input:checked + .check__box {
    display: block;

    position: absolute;
    top: 10px;
    right: 5px;

    width: 46px;
    height: 46px;

    outline: none;
    z-index: 3;
    background: url("../img/sprite/icon-like-2.svg") 50% 50% no-repeat;
    background-color: inherit;
  }

  /* Focused */

  .check__input:focus + .check__box {
    background-image: url("../img/sprite/icon-like-2.svg");
    background-color: inherit;
  }

  .check__input:checked:focus + .check__box {
    background-image: url("../img/sprite/icon-like-2.svg");
    background-color: inherit;
  }
}

.card-flat__box-data {
  padding: 20px 10px;

  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;

  .card-flat__list-data {
    margin: 0;
    margin-bottom: 12px;
    padding: 0;
    list-style: none;

    display: flex;
    flex-direction: row;

    li {
      padding: 5px;
    }

    li:not(:last-child) {
      border-right: 1.5px solid $color-ghost;
    }
  }

  .card-flat__cost {
    margin: 0;
    margin-bottom: 10px;
    padding: 0;

    font-weight: bold;
    font-size: 32px;
    line-height: 34px;
    letter-spacing: 0.25px;
    color: $color-air-force-blue;
    //  ios
    -webkit-text-fill-color: $color-air-force-blue;

    @media (max-width: 350px) {
      font-size: 24px;
      line-height: 26px;
    }
  }

  .card-flat__address {
    margin: 0;
    margin-bottom: 10px;
    padding: 0;

    font-weight: lighter;
    font-size: 18px;
    line-height: 20px;
    letter-spacing: 0.25px;
    color: $color-nero;

    @media (max-width: 350px) {
      font-size: 16px;
      line-height: 22px;
    }
  }

  .card-flat__data {
    margin: 0;
    padding: 0;

    font-weight: bold;
    font-size: 22px;
    line-height: 24px;
    letter-spacing: 0.25px;
    color: $color-night-rider-2;

    @media (max-width: 350px) {
      font-size: 18px;
      line-height: 20px;
    }
  }

  .card-flat__data-title {
    margin: 0;
    padding: 0;

    font-size: 17px;
    line-height: 23px;
    letter-spacing: 0.25px;
    color: $color-night-rider-2;
  }

  .card-flat__btn {
    width: 244px;
    height: 44px;

    font-size: 17px;
    line-height: 100px;
    letter-spacing: 0.25px;
    color: $color-default-white;

    opacity: 0.851;
    background-color: $color-wedgewood;
    border-radius: 6px;
    transition: background-color $default-transition-settings, opacity $default-transition-settings;

    &:hover,
    &:focus {
      background-color: $color-venice-blue;
    }

    &:active {
      opacity: 0.5;
      background-color: $color-venice-blue;
    }
  }
}

//    (Swiper  )

.card-flat__box-swiper,
.card-flat__box-swiper .swiper-container,
.card-flat__box-swiper .swiper-wrapper {
  height: 188px;
  border-top-right-radius: 15px;
  border-top-left-radius: 15px;

  img {
    width: 100%;
    height: 100%;
  }
}

.card-flat__box-swiper {
  position: relative;

  .swiper-pagination {
    top: 0;
    left: 0;

    width: 100%;
    display: flex;
    justify-content: space-around;

    z-index: 2;
  }
}

.card-flat__box-swiper .swiper-pagination-bullet {
  width: 65px;
  height: 188px;

  border-radius: 0;
  background-color: transparent;
  outline: none;
  opacity: 1;
  transition: opacity $default-transition-settings, background-color $default-transition-settings;

  position: relative;

  .card-flat__bullet-line {
    position: absolute;
    bottom: 5px;
    left: 0;

    width: 100%;
    height: 3px;
    background-color: $color-default-white;
    transition: background-color $default-transition-settings;
  }
}

.card-flat__box-swiper .swiper-pagination-bullet-active .card-flat__bullet-line {
  outline: none;
  background-color: $color-wedgewood;
  transition: background-color $default-transition-settings;
}
