.object-list {
  padding-bottom: 72px;

  &__load {
    margin-bottom: 19px;
  }

  &__controls-wrap {
    display: flex;
    width: 100%;
  }

  &__controls {
    display: flex;
    margin-left: auto;
    margin-right: auto;
    min-height: 33px;
    justify-content: center;
    align-items: center;
    padding-top: 5px;
    padding-bottom: 5px;
  }

  &__pagination {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin: 0;
    padding-left: 0;
    list-style: none;
  }

  &__nav {
    margin-left: 15px;
    margin-right: 15px;
    padding-left: 5px;
    padding-right: 5px;
    line-height: 23px;
    font-family: $font-family--r;
    color: $color-wedgewood;
    font-size: 19px;
    font-weight: 500;
    transition: opacity $default-transition-settings;

    &:hover {
      opacity: 0.7;
    }
  }

  &__item-panagination {
    margin-left: 15px;
    margin-right: 15px;
  }

  &__link {
    display: block;
    padding-left: 5px;
    padding-right: 5px;
    line-height: 23px;
    font-family: $font-family--r;
    color: $color-wedgewood;
    font-size: 19px;
    font-weight: 500;
    transition: opacity $default-transition-settings;

    &:hover {
      opacity: 0.7;
    }
  }

  &__link-active {
    cursor: default;
    pointer-events: none;
    background-color: $color-alice-blue;
  }

  &__link-points {
    cursor: default;
    pointer-events: none;
  }
}
