.modal--search-map {
  //   
  img {
    object-fit: cover;
    height: calc(100vh - 89px);
  }

  .modal__content {
    display: flex;
    flex-direction: column;
    width: 100vw;
    min-height: 100vh;
    min-height: calc(100 * var(--vh, 1vh));
    padding: 0;
  }

  .modal__wrapper {
    padding: 0;
  }

  .modal__close-btn {
    right: 50px;
    top: 15px;
    display: block;
    padding: 15px;
    //border: 1px solid #ddd;
    background-color: #ffffff;
    color: rgba(0, 0, 0, 0.5);
    text-align: center;
    font-size: 14px;
    line-height: 38px;
    transition: all 0.15s ease;

    &:focus {
      outline: none;
    }
  }
}

.search-map {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
}

.search-map__map {
  flex-grow: 1;
  width: 100%;
}

.search-map__btn-select {
  position: absolute;
  left: 50px;
  top: 15px;
  padding: 0 15px;
  display: flex;
  align-items: center;
  height: 50px;
  background-color: $color-white;
  border: none;
  border-radius: 25px;
  color: #505152;
  font-size: 14px;
  box-shadow: 0 2px 8px 0 rgba(0, 0, 0, 25%);
  z-index: 2;
  cursor: pointer;

  &:focus {
    outline: none;
  }
}

.search-map__footer {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 20px;

  .button {
    margin: 0 10px;
  }
}

.search-map__input {
  display: none;
}
