.offers {
  padding-top: 26px;
  padding-bottom: 22px;
  overflow: hidden;

  @include viewport--md {
    padding-bottom: 35px;
  }

  @media (max-width: $viewport--xs) {
    padding-left: 20px;
    padding-right: 20px;
  }

  &__swiper {
    overflow: visible;
  }

  &__list-sections {
    @media (min-width: 1366px) {
      display: flex;
      flex-wrap: wrap;
      list-style: none;
      align-items: stretch;
      height: auto;
      padding-left: 0;
      margin: 0;
    }
  }

  &__section {
    margin-right: 15px;
    margin-bottom: 15px;
    padding-top: 18px;
    padding-bottom: 29px;

    width: 285px;
    height: auto;
    min-height: 370px;

    display: flex;
    flex-direction: column;

    border-radius: 6px;
    opacity: 0.9;

    @media (min-width: 1366px) {
      box-shadow: 0 0 4px $color-box-shadow;
      transition: box-shadow $default-transition-settings;

      &:hover {
        box-shadow: 0 0 18px $color-box-shadow;
      }
    }

    //-    mobile
    @include viewport--md {
      margin: 20px;
      padding: 0;
      min-width: 288px;
    }
  }

  .block-cards__item {
    //-    mobile
    @media (max-width: 1365px) {
      &:first-child {
        margin-left: 118px;
      }

      &:last-child {
        min-width: 397px;
      }
    }

    @media (max-width: $viewport--md) {
      &:first-child {
        margin-left: 100px;
      }
    }

    @media (max-width: $viewport--sm) {
      &:first-child {
        margin-left: 60px;
      }
    }

    @media (max-width: $viewport--xs) {
      &:first-child {
        margin-left: 20px;
      }
    }
  }

  //-    mobile
  .offers__shadow {
    @include viewport--md {
      padding-top: 18px;
      padding-bottom: 38px;
      width: 288px;
      border-radius: 15px;
      box-shadow: 0 0 4px $color-box-shadow;
      transition: box-shadow $default-transition-settings;

      &:hover {
        box-shadow: 0 0 18px $color-box-shadow;
      }
    }
  }

  &__link-head {
    display: block;
    margin-bottom: 20px;
    min-height: 222px;

    @media (max-width: $viewport--xs) {
      min-height: 230px;
    }

    h3 {
      margin: 0;
      margin-left: 21px;
      margin-right: 21px;
      margin-bottom: 6px;
      font-family: $font-family--r;
      font-size: 22px;
      line-height: 27px;
      font-weight: 700;
      letter-spacing: 0.025em;
      color: $color-night-rider;
      min-height: 27px;

      @media (max-width: $viewport--xs) {
        font-size: 19px;
        line-height: 23px;
        min-height: 23px;
      }
    }

    p {
      margin: 0;
      margin-left: 22px;
      margin-right: 22px;
      margin-bottom: 14px;
      font-family: $font-family--r;
      font-size: 17px;
      line-height: 22px;
      font-weight: 400;
      letter-spacing: 0.025em;
      color: $color-denim;
      min-height: 22px;

      @media (max-width: $viewport--xs) {
        margin-bottom: 18px;
        font-size: 19px;
        line-height: 20px;
        min-height: 22px;
      }
    }

    img {
      width: 290px;
      height: 153px;
      object-fit: cover;

      @media (max-width: $viewport--xs) {
        width: 290px;
        height: 159px;
      }
    }
  }

  &__table {
    width: 100%;
    border-spacing: 0 5px;
    color: $color-gray-3;

    @media (max-width: $viewport--xs) {
      border-spacing: 0 7px;
    }

    td {
      font-family: $font-family--r;
      font-size: 17px;
      line-height: 19px;
      font-weight: 400;
      letter-spacing: 0.025em;

      @media (max-width: $viewport--xs) {
        font-size: 17px;
        line-height: 17px;
      }
    }

    tr td:first-child {
      width: 76%;
      padding-left: 8.28%;
      padding-right: 3.44%;
      text-align: left;
    }

    tr td:last-child {
      padding-left: 3.44%;
      padding-right: 6.9%;
      text-align: right;
    }
  }

  &__link-table {
    color: $color-denim;
    transition: all 0.3s ease;

    &:hover {
      color: $color-dim-gray;
    }
  }
}
