.modal-filter {
  height: 100%;

  .filter-checkbox {
    label {
      padding: 0;
      padding-left: 25px;
      white-space: nowrap;
    }
  }
}

.modal-filter__wrap {
  max-width: 925px;
}

.modal-filter__row {
  display: flex;
  margin-bottom: 33px;
  padding-left: 15px;
  padding-right: 15px;

  &:last-child {
    // margin-top: 64px;

    @media (max-width: 1200px) {
      margin-top: 0;
    }
  }

  @include viewport--sm {
    display: block;

    &:last-child {
      display: flex;
      flex-direction: column;
    }
  }

  @include viewport--xs {
    padding-left: 0;
    padding-right: 0;
  }

  &--margin {
    margin-bottom: 20px;

    @include viewport--xs {
      margin-bottom: 33px;
    }
  }

  &--right {
    max-width: 800px;
  }
}

.modal-filter__label {
  flex-shrink: 0;
  width: 160px;
  margin-top: 13px;
  margin-right: 13px;
  font-weight: 700;
}

.modal-filter__inputs {
  display: flex;
  flex-wrap: wrap;

  input {
    outline: none;
  }

  &:last-child {
    @include viewport--sm {
      flex-direction: column;
    }
  }

  @include viewport--sm {
    width: 329px;
  }

  @include viewport--xs {
    width: 92%;
  }
}

.modal-filter__item {
  margin-right: 16px;
  margin-top: 13px;

  label {
    line-height: 19px;
  }

  @media (max-width: 1200px) {
    margin-top: 5px;
    margin-bottom: 6px;
  }
}

.modal-filter__dropdown {
  margin: 0;

  @include viewport--sm {
    margin-top: 13px;
  }

  @include viewport--xs {
    margin-top: 8px;
  }
}

.modal-filter__btn {
  padding: 12px 32px;
  margin-right: 20px;
  border-radius: 4px;
  font-size: 14px;
  line-height: 19px;

  @include viewport--xs {
    padding: 10px 28px;
    margin-right: 7px;
  }

  &--clear {
    padding-left: 29px;
    padding-right: 29px;
  }
}

.modal-filter__indent {
  height: 100px;

  @media (max-width: 1200px) {
    height: 150px;
  }

  @include viewport--xs {
    height: 100px;
  }
}

.modal-filter .filter-dropdown--modal {
  @include viewport--xs {
    width: 100%;
  }
}
